/* color vars
 * may not be totally x-browser safe but yolo
 */
html {
  --darkBlue: #2C3E50;
  --red:      #E74C3C;
  --white:    #ECF0F1;
  --skyBlue:  #3498DB;
  --blue:     #2980B9;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

button {
  -webkit-appearance: none;
  color: var(--white);
  background: none;
  border: none;
  outline: none;
}

html, body {
  background: var(--darkBlue);
  color: var(--white);
  font-size: 16px;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


h1 {
  color: var(--red);
}

.view--title {
  font-size: 1.4rem;
  margin: 1em 0 2em;
}

.view--title h4 {
  margin: 0;
}

.view .btn-change-mode {
  color: var(--skyBlue);
}

#root {
  text-align: center;
  color: var(--white);
  width: 100%;
  overflow-x: hidden; /* for sidebar slideout */
}

.input-box {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 0 2rem;
  background: var(--darkBlue);
}

.input-box .label {
  font-size: 1.1rem;
  margin: 0 0 0.5rem;
}

.input-box .input {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  width: 90%;
  padding: 12px;
  font-size: 30px;
  height: 56px;
  border: none;
  background: var(--skyBlue);
  border-radius: 4px;
  color: white;
}

.input-box.locked .input {
  background: var(--blue);
}

.input-box.focus {
  position: absolute;
  top: 0;
  height: 100%;
  z-index: 1;
}

.input-box.focus .input {
  background: var(--red);
  outline: none;
}


.keypad {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-left: 1px solid var(--white);
  border-bottom: 1px solid var(--white);
  z-index: 1;
  height: 60%;
}

.keypad-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 0 33%;
      flex: 1 0 33%;
  background: var(--skyBlue);
  border: 1px solid var(--white);
  border-bottom: none;
  border-left: none;
  font-size: 32px;
  font-weight: 800;
}



/**
 * inputbox pre-populated values.
 *
 */

.input-box-pre-values .btn-choose {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1.25rem;
  color: var(--skyBlue);
}


.input-box-pre-values.focus {
  z-index: 2; /* above keypad */
  background: var(--darkBlue);
}

.input-box-pre-values .list .list-item {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1.5rem;
  background: var(--blue);
}

.input-box-pre-values .list .list-item:nth-child(even) {
  background: var(--skyBlue);
}

/** drinks view */
.drinks {
  padding: 12px;
}

.drinks .how-much-since {
  font-size: 1.4rem;
  line-height: 1.1em;
}

.drinks .how-much-since b {
  white-space: nowrap;
  font-weight: 800;
}

.drinks .drinks-list {
  margin: 0 0 32px;
}

/** drinks summary header */
.drinks-summary p {
  font-weight: 600;
  font-size: 1rem;
  margin: 0;
}

/** individual drink */
.drink {
  background: var(--skyBlue);
  padding: 8px;
  border-top: 1px solid var(--darkBlue);
  color: var(--darkBlue);
}

.drink:nth-child(even) {
  background: var(--blue);
}

.drink .meta, .drink .actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.drink .meta .timestamp, .drink .meta .vol-abv {
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
  text-align: left;
}

.drink .meta p {
  margin: 0;
}

.drink .meta .sd {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  text-align: right;
}


.drink .actions {
  margin: 16px 0 0;
}


/** sidebar slideout thing */

.sidebar-wrapper {
  display: -ms-flexbox;
  display: flex;
  width: 300%;
  -webkit-transform: translateX(-33.3333334%);
      -ms-transform: translateX(-33.3333334%);
          transform: translateX(-33.3333334%);
  -webkit-transition: -webkit-transform 0.4s ease-in-out;
  transition: -webkit-transform 0.4s ease-in-out;
  -o-transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}

.sidebar-wrapper.left {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.sidebar-wrapper.right {
  -webkit-transform: translateX(-66.6666667%);
      -ms-transform: translateX(-66.6666667%);
          transform: translateX(-66.6666667%);
}

.sidebar-wrapper > div {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: -ms-flexbox;
  display: flex;
}

/* buttons  */

.btn {
  background: white;
  border-radius: 2px;
  padding: 8px;
  color: var(--darkBlue);
  font-size: 1rem;
}



#root, #router, .App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.App {
  -ms-flex-direction: column;
      flex-direction: column;
}

.App > header {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  display: -ms-flexbox;
  display: flex;
  background: var(--blue);
  border-bottom: 2px solid white;
  padding: 8px;
}

.App > header .drinks-summary {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App .sidebar-wrapper {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}


.view {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%; /* height */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 3%;
}

.keypad {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.calc-form {
  position: relative;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}


.settings .success-notice {
  color: var(--white);
  line-height: 34px;
}

.calculator--btn-switch {
  margin: 0.5em 0;
  color: var(--skyBlue);
  font-size: 1rem;
}


/* splash */
.splash {
  text-align: center;
  padding: 32px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-animation: fadein 0.5s ease-in-out;
          animation: fadein 0.5s ease-in-out;
}

h1 {
  color: var(--skyBlue);
  font-weight: 200;
  font-size: 2rem;
}

.splash h1 {
  margin-top: 25%;
}


/* spinner */
.spinner svg {
  -webkit-animation: rotate 2s cubic-bezier(0.5, 0.35, 0.5, 0.65) infinite;
          animation: rotate 2s cubic-bezier(0.5, 0.35, 0.5, 0.65) infinite;
}


/* ButtonLoader */
.button-loader .wrapper {
  position: relative;
}

.button-loader .spinner, .button-loader .button-loader-children {
  -webkit-transition: opacity 0.1s ease-in-out;
  -o-transition: opacity 0.1s ease-in-out;
  transition: opacity 0.1s ease-in-out;
}

.button-loader .spinner {
  position: absolute;
  width: 100%;
  text-align: center;
}

.button-loader .spinner {
  opacity: 0;
}

.button-loader.loading .button-loader-children {
  opacity: 0;
}

.button-loader.loading .spinner {
  opacity: 1;
}


/* animations */
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}



